     <div class='m-2 p-2 bg-grey border border-primary text-left' id='faq'>
        <div class='row m-2'>
            <h2>FAQ</h2>
        </div>
        <div class='row m-2'>
            <div>
                <p><b>The 'Data Recorder' stops after 5000 data points, how do I collect more?</b></p>
                <p> 
                    1) Once 5000 data points have been recorded, you can simply download those data points with the 'Download CSV' button; reset the data by clicking 'Reset' (next to 'Download CSV'); 
                    and then begin recording again. Continuing to download data in batches like this will allow you to record and save as many data points as you need.  
                </p>
                <p>
                    2) Alternatively, if you are using the data snapshot tool to record data, simply click 'Reset' next to 'Download CSV' and then begin recording again - the data snapshot information will be retained and new snapshots 
                    can be added to the previous ones by clicking 'Record Snapshot'. All recorded snapshots can then be downloaded (as a CSV file) by clicking 'Download Snapshots'.
                </p>
                <p>
                    The limit in data points recorded in a single batch exists to minimise potential lag, particularly in systems with less memory.
                </p>
            </div>
            <div>
                <p><b>I am experiencing some lag in the system, how do I reduce this?</b></p>
                <p> 
                    Earlier versions of the system had unlimited data recording and therefore lag could be introduced as more data points were stored and rendered on the graph. The system now has a limit on the data recordable and optimisations 
                    in how data is updated on the graph have been included which should mean that no lag is experienced. However, if you are using a low memory system (such as a mobile) you may want to minimise the run time of any recorded experiment, 
                    keeping your data point count as low as you can, before downloading data and resetting for a new run.  
                </p>
                
            </div>
            <div>
                <p><b>If I reset the data will I lose my snapshots?</b></p>
                <p> 
                    No - if you use the 'Reset' button next to 'Download CSV' then your data snapshots will remain on screen. However, they will be reset if you use the 'Reset' button in the data snapshot component itself. 
                </p>
            </div>
            <div>
                <p><b>I can't see some UI elements, how do I add them?</b></p>
                <p> 
                    Some components are not always shown on the UI but must be added from the menu. Select the component that you want to add from the Menu bar at the top of the screen. You can remove components 
                    by selecting that component again in the menu.
                </p>
            </div>
            <div>
                <p><b>How do I plot the step or ramp input or expected output?</b></p>
                <p> 
                    The 'Plot function' is part of the 'Graph' UI element and can be used to plot the necessary input and theoretical output functions. The expected set point can be plotted with a horizontal line
                    by setting the function to 'Linear' and then inputting 'a' = 0 and the step value in the 'b' input. The input ramp function can be plotted by setting the function to 'Linear' 
                    and then entering the ramp gradient in 'a' and 0 in 'b'. The theoretical output to a step or ramp function can also be plotted using the 'Step (1st Order)', 'Step (2nd order)' and 'Ramp (1st order)' functions. Details of these 
                    are given in the <a href='#plottingfunctions'>'Plotting Functions'</a> section.
                </p>
            </div>
            <div>
                <p><b>How do I use the ruler and protractor tools?</b></p>
                <p> 
                    Select the ruler or protractor from the 'Tools' menu in the blue menu bar at the top of the screen. The tools can be rotated by holding the 'o' key whilst
                    clicking and dragging the tool. When a tool is on screen you cannot interact with the UI beneath it until you press 'w' to switch between tool and UI. Alternatively you can 
                    just use the 'Clear Workspace' button in the menu bar to remove the tools completely from the screen. The ruler tool will scale with screen size in order to maintain the correct size of the motor disk. 
                </p>
            </div>
            <div>
                <p><b>How do you save graph images?</b></p>
                <p> You can right click on the graph canvas and choose 'Save Image As..' or you can use a screenshot tool (eg 'Snipping Tool' on Windows or 'Screenshot' on Ubuntu).</p>
            </div>
            <div>
                <p><b>How do you start and stop recording data?</b></p>
                <p> Once you have setup your experiment, (in Step or Ramp mode) clicking the green 'Run' button will start the motor running and automatically start collecting data. Clicking the red 'Stop' button on the 'Data Recorder' will then 
                    stop collecting data - but the hardware will continue to run until you click 'Stop' on the control panel or 'Exit mode'. In 'Free' mode, you will need to click 'Record' manually to start recording data.
                </p>
                <div class='p-4 text-right'><a href='#topofpage'>Back to top</a></div>
            </div>
                
        </div>
    </div>


    <div class='row m-2 p-2 bg-grey border border-primary text-left' id='webcam'>
        <div class='row m-2'>
            <div class='col-4'>
                <img src="../../public/images/Webcam_with_Thermometer.png" class="img-thumbnail">
            </div>
            <div class='col-8'>
                <h2>Webcam view</h2>
                <p>
                    Each remote lab has a webcam streaming live video. Each lab has one of two different views: either a front on view or a more top down view using a mirror. The 
                    UI ruler tool has been scaled appropriately for both of these views. Within the view is also a digital thermometer output and a maximum temperature before which the motor 
                    will automatically shutdown.
                </p>
                <div class='p-4 text-right'><a href='#topofpage'>Back to top</a></div>
            </div>
        </div>

    </div>

    <div class='row m-2 p-2 bg-grey border border-primary' id='smoothiegraph'>
        <div class='row m-2 text-left'>
            <div class='col-4'>
                <img src="../../public/images/Smoothie.png" class="img-thumbnail">
            </div>
            <div class='col-8'>
                <h2>Live data stream</h2>
                <p>
                    A chart below the webcam view streams live data from the spinning disk hardware: either angular displacement (between -1 and 6.28 radians) or angular velocity (between -400 and 400 rad/s) 
                    depending on which mode has been selected. Although useful for testing and demonstrating the output of the hardware, the <a href='#recorddata'>Data Recorder</a> along with the <a href='#graph'>Graph</a>, <a href='#table'>Table</a> and <a href='#snapshot'>Snapshot</a> components 
                    should be used for recording and analysing data output.
                </p>
                <div class='p-4 text-right'><a href='#topofpage'>Back to top</a></div>
            </div>
        </div>
    </div>

    

    <div class='row m-2 p-2 bg-grey border border-primary text-left' id='controlpanel'>
        <div class='row m-2'>
            <div class='col-4'>
                <img src="../../public/images/ControlPanel_Update.png" class="img-thumbnail">
            </div>
            <div class='col-8'>
                <h2>Control Panel</h2>
                <p>
                    The spinning disk remote lab can be run in three different modes: position (PID), velocity (PID) and voltage (open loop). The PID modes have feedback control loops with PID controllers; whereas, as the name suggests, the voltage (open loop) mode 
                    has no feedback loop and can be used to explore the characteristics of the motor/disk plant itself. All three of these modes will accept 'Step', Ramp' or 'Free' input, which can be selected with the dropdown 'Input type' menu.
                </p>
                
            </div>
        </div>

        <div class='row m-2' id='stepinput'>
            <div class='col-4'>
                <img src="../../public/images/StepInputHardware.png" class="img-thumbnail">
            </div>
            <div class='col-8'>
                <h2>Step Input</h2>
                <p>
                    Step inputs simply need to be typed into the input field and then click 'Run' to send the step signal to the hardware. The hardware will only accept values within a specific range and an appropriate range is provided on the UI. Each control mode accepts steps in different units: position in radians; velocity in 
                    rad/s; and voltage in volts. The step input is of the form y = 0 for t &lt; 0; y=step size for t &ge; 0. The step function can be plotted with the 'Graph Input' component found in Menu > Graph Input. Alternatively, it can be plotted using the 'Plotting Functions' tool on the graph: a step function can be plotted as 
                    a 'Linear' function with 0 gradient and y-intercept equal to the step.
                </p>
                <p>
                    In 'Step' mode, data recording will automatically start when the hardware is started. Therefore, there is no need to manually begin recording and, in fact, the 'Record' button is hidden in this mode as it is not necessary.
                </p>
            </div>
        </div>

        <div class='row m-2' id='rampinput'>
            <div class='col-4'>
                <img src="../../public/images/RampInput.png" class="img-thumbnail">
            </div>
            <div class='col-8'>
                <h2>Ramp Input</h2>
                <p>
                    Ramp inputs simply need to be typed into the input field and then click 'Run' to send the ramp signal to the hardware. The hardware will reach position and velocity limits, so large values of ramp are discouraged. Each control mode accepts ramp gradients in different units: position in rad/s; velocity in 
                    rad/s<sup>2</sup>; and voltage in V/s. The ramp input is of the form y = 0 for t &lt; 0; y=ramp_gradient*t for t &ge; 0. The ramp function can be plotted with the 'Graph Input' component found in Menu > Graph Input. Alternatively, it can be plotted with the 'Plotting Functions' tool on the graph: a ramp function can be plotted as 
                    a 'Linear' function with gradient equal to the ramp gradient and y-intercept equal to 0.
                </p>
                <p>
                    In 'Ramp' mode, data recording will automatically start when the hardware is started. Therefore, there is no need to manually begin recording and, in fact, the 'Record' button is hidden in this mode as it is not necessary.
                </p>
            </div>
        </div>

        <div class='row m-2' id='freeinput'>
            <div class='col-4'>
                <img src="../../public/images/ControlPanel.png" class="img-thumbnail">
            </div>
            <div class='col-8'>
                <h2>Free Input</h2>
                <p>
                    Free input allows specific positions and velocities to be set rather than providing a step or ramp magnitude. All 'free' inputs are limited to specific ranges, but continuous input within that range is possible. In voltage (open loop) mode, the motor voltage will be automatically set 
                    as soon as the slider position is set; whereas in position and velocity modes the slider should be set and then 'Set' clicked to send the signal to the hardware.
                </p>
                <p>
                    In 'Free' mode, data recording needs to be started manually. Starting the hardware will not automatically begin recording. The green 'Record' button must be used to manually start recording data. This is because 'Free' mode is intended more for viewing basic motor behaviours with the webcam and 
                    live data chart rather than recording runs for analysis.
                </p>
                <div class='p-4 text-right'><a href='#topofpage'>Back to top</a></div>
            </div>
        </div>

        <div class='row m-2' id='freeinput'>
            <div class='col-4'>
                <img src="../../public/images/PIDParameters.png" class="img-thumbnail">
            </div>
            <div class='col-8'>
                <h2>PID Parameters</h2>
                <p>
                    Whilst in a PID mode (either Velocity or Position) the 'PID Parameter' input tool will automatically display. The inputs correspond to the 'proportional (K<sub>p</sub>)', 'integral (K<sub>i</sub>)' and 'derivative (K<sub>d</sub>)' terms of a PID controller. The values default to a unity gain controller, 
                    K<sub>p</sub> = 1, K<sub>i</sub> = 0 and K<sub>d</sub> = 0. Clicking 'Reset' will return the values to these defaults. When a new input value is entered the parameters will be automatically sent to the hardware if either 'Enter' is pressed or the input loses focus - i.e. you click somewhere else on the UI.
                </p>
                <div class='p-4 text-right'><a href='#topofpage'>Back to top</a></div>
            </div>
        </div>

    </div>


     <div class='row m-2 p-2 bg-grey border border-primary text-left' id='recorddata'>
        
        <div class='row m-2'>
            <div class='col-4'>
                <img src="../../public/images/DataRecorder.png" class="img-thumbnail">
            </div>
            <div class='col-8'>
                <h2>Data Recording</h2>
                <p>
                   In order to display data on the graph or table or to download CSV data or use the snapshot recorder, the UI needs to be recording. In 'Step' and 'Ramp' input modes, data will start recording automatically when 'Run' is clicked on the Control Panel. In 'Free' mode the 'Record' button must be clicked 
                   prior to making changes if data is to be recorded. There is a maximum number of data points that can be recorded in any run, but it is advisable to click 'Stop' when you have collected sufficient data to show the behaviour you were investigating. All the data collected can be downloaded by clicking 'Download CSV' 
                   and then saving the file. This .csv file can easily be opened in spreadsheet software like Excel or used in data packages in R, MATLAB/Octave or Python.
                </p>
                <div class='p-4 text-right'><a href='#topofpage'>Back to top</a></div>
            </div>
        </div>
    </div>

    <div class='row m-2 p-2 bg-grey border border-primary text-left' id='graph'>
        <div class='row m-2'>
            <div class='col-4'>
                <img src="../../public/images/EmptyGraph.png" class="img-thumbnail">
            </div>
            <div class='col-8'>
               <h2>Graph</h2>
                <p>The graph will automatically set whether it is plotting angle (theta) or angular velocity (omega) 
                    depending on the mode that the simulation has been set to. The <a href='#units'>units</a> default to radians for angle measurements and radians per second for angular speeds.
                    The gradient of a tangent to the plotted curve can also be measured - <a href='#gradient'>see below</a>.
                </p>
            </div>
        </div>

        <div class='row m-2'>
            <div class='col-4'>
                <img src="../../public/images/GraphDataPoint.png" class="img-thumbnail">
            </div>
            <div class='col-8'>
                <p>
                    Once the graph is plotted, hovering the mouse pointer over a data point will toggle a popup giving the specific {time, value} data. If the 'Table' component is open, clicking a data point will take 
                    you to the corresponding line in the <a href='#table'>table</a>.
                </p>
            </div>
        </div>

        <div class='row m-2' id='units'>
            <div class='col-4'>
                <img src="../../public/images/Units.png" class="img-thumbnail">
            </div>
            <div class='col-8'>
                <h4>Units</h4>
                <p>
                    The UI will default to the correct unit for the motor mode selected: rad/s for speed modes and radians for position mode. It is possible to change to RPM and degrees using the unit dropdown on the graph UI. 
                    Although the graph will only plot either an angular position or speed, using the 'Table' or downloading the data will give both angle and angular speed data for a single recorded run.
                </p>
            </div>
        </div>

        <div class='row m-2' id='gradient'>
            <div class='col-4'>
                <img src="../../public/images/Gradient.png" class="img-thumbnail">
            </div>
            <div class='col-8'>
                <h4>Gradient</h4>
                <p>
                    Clicking and dragging on the graph will draw a red line, the gradient of which will be displayed in the gradient output. This is intended as a quick reference and should not be considered as an accurate calculation. 
                    This allows for quick measurement of tangent gradients to motor response curves. 
                    Ensure your starting and ending point are not actually on a graph data point though as hovering a data 
                    point refreshes the graph image and your gradient line will disappear (this is a known issue to be fixed in a later version of the UI).
                </p>
                <div class='p-4 text-right'><a href='#topofpage'>Back to top</a></div>
            </div>
        </div>

    </div>

    <div class='row m-2 p-2 bg-grey border border-primary text-left' id='snapshot'>
        <div class='row m-2'>
            <div class='col-4'>
                <img src="../../public/images/Snapshot.png" class="img-thumbnail">
            </div>
            <div class='col-8'>
                <h2>Snapshot</h2>
                <p>
                    If you only want a quick insight into how the data is changing over time the 'Snapshot recorder' can be used. Whilst data is being recorded current data output from the hardware will be displayed on the top line of the tool, in green. Clicking 'Record Snapshot' will save 
                    the data at the moment the button is pressed and store it on screen. This can be repeated to build up a series of snapshots of the data. 'Command' gives the value that has been sent to the hardware; 
                    Drive the voltage value being sent to the motor from the controller; and Error = command - output. The snapshots can be cleared with the 'Reset' button. This data can be downloaded separately from the full set of data using the 'Download Snapshots' button.
                </p>
                <div class='p-4 text-right'><a href='#topofpage'>Back to top</a></div>
            </div>
        </div>

    </div>

    <div class='row m-2 p-2 bg-grey border border-primary text-left' id='plottingfunctions'>
        <div class='row m-2'>
            <div class='col-4'>
                <img src="../../public/images/FunctionPlotting_Update.png" class="img-thumbnail">
            </div>
            <div class='col-8'>
                <h2>Plotting functions</h2>
                <p>
                    A number of different functions can be plotted on the graph in order to compare against motor output. 'Plot' and 'Clear' only control the plotted functions, which show as red dots, and will not affect the plotted 
                    motor data (black dots). Multiple plots can be placed on the same graph simply by changing the input variables and clicking 'Plot' again. Some examples of common plots are given below.
                </p>
            </div>
        </div>

         <div class='row m-2'>
            <div class='col-4'>
                <img src="../../public/images/Step_with_output_Plotting.png" class="img-thumbnail">
            </div>
            <div class='col-8'>
                <h4>Step input</h4>
                <p>
                    Plotting the set input step (position or angular velocity) on the graph can be achieved as shown in the image to the left: the function is 'Linear' and in this case the set angle was 2.00 radians.
                </p>
            </div>
        </div>

        <div class='row m-2'>
            <div class='col-4'>
                <img src="../../public/images/Step_Plotting.png" class="img-thumbnail">
            </div>
            <div class='col-8'>
                <h4>Step output - 1st Order plant</h4>
                <p>
                    In order to plot the expected step response of the motor to a step voltage input, choose the 'Step (1st Order)' function. The output angular velocity is for a first order plant to a step input 
                    <img src="../../public/images/StepEquation.png" width='120' height='40'>. The step size is in volts, K is the motor gain, &tau; is the motor time constant, and t<sub>0</sub> is the step start time.
                </p>
            </div>
        </div>

        <div class='row m-2'>
            <div class='col-4'>
                <img src="../../public/images/Step2nd_Plotting.png" class="img-thumbnail">
            </div>
            <div class='col-8'>
                <h4>Step output - 2nd Order plant</h4>
                <p>
                    The theoretical step response of a second order plant (position PID mode) can also be plotted by selecting 'Step (2nd Order)' whilst in position PID mode - this plotting function will not show whilst in voltage or 
                    pid speed modes. The second order transfer function is displayed along with inputs for the corresponding values: position step size; damping ratio, &zeta;; natural frequency, &omega;<sub>0</sub>; and step start time, t<sub>0</sub>.
                </p>
            </div>
        </div>
        
        <div class='row m-2'>
            <div class='col-4'>
                <img src="../../public/images/Ramp_output_Plotting.png" class="img-thumbnail">
            </div>
            <div class='col-8'>
                <h4>Ramp input</h4>
                <p>
                    The ramp input function can be plotted using a 'Linear' function. Simply input the theoretical ramp gradient as 'a' with b=0. This allows the steady state error to be investigated.
                </p>
                <div class='p-4 text-right'><a href='#topofpage'>Back to top</a></div>
            </div>
        </div>

        <div class='row m-2'>
            <div class='col-4'>
                <img src="../../public/images/Ramp_Plotting.png" class="img-thumbnail">
            </div>
            <div class='col-8'>
                <h4>Ramp output</h4>
                <p>
                    In order to plot the expected ramp response of the motor to a ramp input, choose the 'Ramp (1st Order)' function. The output angular velocity is for a first order plant to a ramp input 
                    <img src="../../public/images/RampEquation.png" width='120' height='40'>. The ramp gradient is in volts/s, K is the motor gain, &tau; is the motor time constant, and &omega;<sub>0</sub> is the initial angular velocity.
                </p>
                <div class='p-4 text-right'><a href='#topofpage'>Back to top</a></div>
            </div>
        </div>

    </div>

    <div class='row m-2 p-2 bg-grey border border-primary text-left' id='table'>
       <div class='row m-2'>
            <div class='col-4'>
                <img src="../../public/images/TableoutputHARDWARE.png" class="img-thumbnail">
            </div>
            <div class='col-8'>
                <h2>Data table</h2>
                <p>
                    To see a table of the recorded data select 'Table' from the <a href='#menubar'>Menu</a> dropdown at the top of the screen. The table includes both the angle and angular velocity over the time recorded. It 
                    also includes the proportional Command, Drive and Error (<a href='#snapshot'>explained above</a>). Clicking on a data point on the graph will link directly to the corresponding line
                    in the data table and highlight it in red. 
                </p>
                <div class='p-4 text-right'><a href='#topofpage'>Back to top</a></div>
            </div>
        </div>
    </div>

    <div class='row m-2 p-2 bg-grey border border-primary text-left' id='menubar'>
        
        <div class='row m-2'>
            <div class='col-4'>
                <img src="../../public/images/MenuBar.png" class="img-thumbnail">
            </div>
            <div class='col-8'>
                <h2>Menu bar</h2>
                <p>
                   The 'Menu' dropdown allows you to add additional components to the UI. Currently this includes a component for displaying the input functions; diagrams for the PID control loop; the snapshot recorder; displaying the data in table format and a simple stopwatch tool. 
                   Components are removed from the UI by selecting them again in the menu.
                
                </p>
                <p>
                    The <a href='#tools'>Tools'</a> dropdown allows you to add UI tools to the screen for making measurements of the remote hardware. Currently this includes a ruler and protractor tool.
                </p>
                <div class='p-4 text-right'><a href='#topofpage'>Back to top</a></div>
                
            </div>
        </div>


    </div>

    <div class='row m-2 p-2 bg-grey border border-primary text-left' id='tools'>
        
        <div class='row m-2'>
            <div class='col-4'>
                <img src="../../public/images/UsingRuler.png" class="img-thumbnail">
            </div>
            <div class='col-8'>
                <h2>Tools</h2>
                <p>
                    Currently this includes a ruler and protractor tool. Whilst a tool is on screen the underlying 
                    UI elements cannot be interacted with. However, pressing 'w' will switch between the tools and the UI. Alternatively the tools can be cleared from the screen by clicking 'Clear Workspace'. The ruler tool will automatically 
                    scale to the window size in order to maintain the correct measurement of disk size within the webcam view. 
                </p>
                <h4>Hotkeys:</h4>    
                <p>
                    'w' to switch between interacting with tools and underlying UI.
                </p>
                <p>
                    'o' to rotate the tools. Hold 'o' whilst clicking and dragging a tool to rotate it.
                </p>
                <div class='p-4 text-right'><a href='#topofpage'>Back to top</a></div>
            </div>
        </div>
