        <div class='row m-2'>
            <div class='col-4'>
                <img src="../../public/images/Stopwatch.png" class="img-thumbnail">
            </div>
            <div class='col-8'>
                <h2>Stopwatch</h2>
                <p>
                    A simple stopwatch tool which may or may not come in useful. The time displayed here is not recorded or connected in any way to the hardware.
