    </div>


   



  </div>
</template>

<script>
export default {
  name: 'SpinningDiskInfo',
  props: {
    
  },
  data(){
      return{
          full_ui_image: null,
          full_ui_image_size: {width:1847, height:1022},
          webcam_orig_coords: {x_top:26, y_top:68, x_bottom:900, y_bottom:680},
          webcam_image_coords: '',
          control_panel_orig_coords: {x_top:144, y_top:914, x_bottom:774, y_bottom:972},
          control_panel_image_coords: '',
          data_recorder_orig_coords: {x_top:950, y_top:66, x_bottom:1290, y_bottom:125},
          data_recorder_image_coords: '',
          menu_bar_orig_coords: {x_top:0, y_top:0, x_bottom:1841, y_bottom:54},
          menu_bar_image_coords: '',
          graph_coords: {x_top:947, y_top:380, x_bottom:1838, y_bottom:750},
          graph_image_coords: '',
          function_plotting_coords: {x_top:1496, y_top:792, x_bottom:1764, y_bottom:926},
          function_plotting_image_coords: '',
          units_coords: {x_top:959, y_top:830, x_bottom:1166, y_bottom:917},
          units_image_coords: '',
          gradient_coords: {x_top:1187, y_top:831, x_bottom:1391, y_bottom:909},
          gradient_image_coords: '',
          snapshot_coords: {x_top:948, y_top:209, x_bottom:1835, y_bottom:350},
          snapshot_image_coords: '',
          smoothie_coords: {x_top:33, y_top:744, x_bottom:884, y_bottom:864},
          smoothie_image_coords: '',
          stopwatch_coords: {x_top:1343, y_top:66, x_bottom:1682, y_bottom:194},
          stopwatch_image_coords: '',
          input_select_coords: {x_top:375, y_top:980, x_bottom:555, y_bottom:1011},
          input_select_image_coords: '',
      }
  },
  computed:{
      
  },
  created(){
      window.addEventListener('resize', this.updateImageMap);
  },
  mounted(){
      this.full_ui_image = document.getElementById('fullui_image');
      setInterval(() => {this.updateImageMap()}, 100);
  },
  methods:{
      updateImageMap(){
          this.webcam_image_coords = this.convertCoords(this.webcam_orig_coords);
          this.control_panel_image_coords = this.convertCoords(this.control_panel_orig_coords);
          this.data_recorder_image_coords = this.convertCoords(this.data_recorder_orig_coords);
          this.menu_bar_image_coords = this.convertCoords(this.menu_bar_orig_coords);
          this.graph_image_coords = this.convertCoords(this.graph_coords);
          this.function_plotting_image_coords = this.convertCoords(this.function_plotting_coords);
          this.units_image_coords = this.convertCoords(this.units_coords);
          this.gradient_image_coords = this.convertCoords(this.gradient_coords);
          this.snapshot_image_coords = this.convertCoords(this.snapshot_coords);
          this.smoothie_image_coords = this.convertCoords(this.smoothie_coords);
          this.stopwatch_image_coords = this.convertCoords(this.stopwatch_coords);
          this.input_select_image_coords = this.convertCoords(this.input_select_coords);
      },
      convertCoords(orig_coords){
          let coords = '';
          if(this.full_ui_image != null){
              let new_x_top = (orig_coords.x_top/this.full_ui_image_size.width)*this.full_ui_image.width;
                let new_y_top = (orig_coords.y_top/this.full_ui_image_size.height)*this.full_ui_image.height;
                let new_x_bottom = (orig_coords.x_bottom/this.full_ui_image_size.width)*this.full_ui_image.width;
                let new_y_bottom = (orig_coords.y_bottom/this.full_ui_image_size.height)*this.full_ui_image.height;

                coords += new_x_top.toString();
                coords += ',';
                coords += new_y_top.toString();
                coords += ',';
                coords += new_x_bottom.toString();
                coords += ',';
                coords += new_y_bottom.toString();
          }
          
          
          return coords;

      },
  },
}
</script>

